



import React from 'react';
import { Row, Col, Card, Table, Tabs, Tab, Badge } from 'react-bootstrap';

import Loader from '../../App/layout/Loader/index'

import { getVendor, deleteVendors, getVendors } from '../../api/inventory/vendorApi';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import DeleteModal from '../../App/components/Reusables/DeleteModal';
import { getPurchaseOrders } from '../../api/inventory/purchaseOrder';
import { getStartEnd } from '../../helpers/getStartEnd';
import Paging from '../../App/components/Reusables/Paging';
import Spinner from '../../App/components/Reusables/Spinner';
import Num from '../../App/components/Reusables/Num';
import isEmpty from '../../validation/isEmpty';
import IsEmpty from '../../App/components/Reusables/IsEmpty';




const Vendor = (props) => {


    const [vendor, setVendor] = React.useState(null);
    const [loading, setLoading] = React.useState(true);
    const [deleteData, setDeleteData] = React.useState({ showModal: false, vendorId: null });

    const [purchaseOrdersArr, setPurchaseOrdersArr] = React.useState([]);
    const [instanceCount, setInstanceCount] = React.useState(0)

    React.useEffect(() => {
        const controller = new AbortController();
        //Api call to get the data of user
        getVendor({ action: 'get', vendor_id: props.match.params.id, filter: 'none' })
            .then(async (res) => {
                console.log(res.json)
                if (res.json.status) {
                    await setVendor(res.json.vendors[0])
                    setLoading(false)
                }
            }).catch((err) => {
                console.log(err);
            });

        getPurchaseOrders({
            "action": "get",
            "filter": "multiple",
            filters: {
                "date": false,
                'start_date': null,
                'end_date': null,
                'vendor': props.match.params.id,
                'purchaseOrderStatus': false,
                'status': false
            },
            ...getStartEnd(props)
        }, controller.signal)
            .then((res) => {
                console.log(res)
                if (res.json.status) {
                    setPurchaseOrdersArr(res.json.purchase_orders);
                    setInstanceCount(res.json.count);
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            })
            .catch((err) => {
                console.log(err)
            });
        return () => controller.abort();
    }, [])

    const _triggerDeleteModal = (e) => {
        setDeleteData({ showModal: true, vendorId: props.match.params.id });
    }



    const _delete = (idToBeDeleted) => {
        deleteVendors({ action: 'delete', vendors_id: [idToBeDeleted] })
            .then(res => res.json)
            .then((res) => {
                if (res.status) {
                    Jhilikka.fire({ icon: 'success', title: 'Vendor deleted successfully!', background: '#dffff0' })
                    props.history.push('/vendors')
                }
            }).catch((err) => {
                console.log(err);
            });
    }

    const PurchaseOrdersList = purchaseOrdersArr.map((order, index) => {
        return (
            <tr key={index}

                style={{ cursor: 'pointer' }}
                onClick={(e) => {
                    props.history.push('/purchase-order/' + order.id)
                }}
            >
                <th scope="row">{order.id}</th>

                {/* <td style={{ maxWidth: '200px', overflow: 'hidden', paddingRight: '50px' }}>{order.items}</td> */}
                <td>{(new Date(order.invoiced_on)).toDateString()}</td>
                <td><Num>{order.total_cost}</Num></td>
                <td>{order.status_name}</td>
            </tr>
        )
    })

    if (loading) return <Loader />

    return (
        <>
            <Row className="d-flex justify-content-between pageHead">
                <h4 className="m-b-10" >{vendor.first_name} {vendor.middle_name}{vendor.last_name}</h4>
                <div className="topButtonWrapper">
                    <button
                        className="btn btn-primary btn-sm shadow-2 mb-4"
                        onClick={() => props.history.push(`/vendor/update/${props.match.params.id}`)}
                    > <i className="feather icon-edit text-c-white f-15 m-r-5" />Update</button>
                    <button
                        className="btn btn-danger btn-sm shadow-2 mb-4"
                        onClick={_triggerDeleteModal}
                    > <i className="feather icon-trash-2 text-c-black f-15 m-r-5" />Delete</button>
                </div>
            </Row>
            <Row>
                <Col xl={12}>
                    <Card>
                        <Card.Body>
                            <h5 className='mb-4'>Vendor Profile</h5>
                            <div className="row d-flex align-items-center">

                                <Col md={2}></Col>
                                <Col md={8}>


                                    <div className='mb-1 border-bottom'><b>Name</b></div>
                                    <div className='mt-1 mb-3'>
                                        {vendor.first_name} {vendor.middle_name} {vendor.last_name} {" "}
                                        {vendor.is_active ? (<Badge variant="info">Active</Badge>) : (<Badge variant="secondary">Inactive</Badge>)}
                                    </div>


                                    <div className='mb-1 border-bottom'><b>Tax Number</b></div>
                                    <div className='mt-1 mb-3'>{vendor.tax_number}</div>


                                    <div className='mb-1 border-bottom'><b>Email</b></div>
                                    <div className='mt-1 mb-3'>{vendor.email}</div>

                                    <div className='mb-1 border-bottom'><b>Website</b></div>
                                    <div className='mt-1 mb-3'>{vendor.website}</div>

                                    <div className='mb-1 border-bottom'><b>Address</b></div>
                                    <div className='mt-1 mb-3'>{vendor.address}</div>

                                    <div className='mb-1 border-bottom'><b>Phone</b></div>
                                    <div className='mt-1 mb-3'>{vendor.phone1}, {" "} {" "}{vendor.phone2}</div>


                                    <div className='mb-1 border-bottom'><b>Company</b></div>
                                    <div className='mt-1 mb-3'>{vendor.company}</div>

                                    <div className='mb-1 border-bottom'><b>Country</b></div>
                                    <div className='mt-1 mb-3'>{vendor.country_detail.name}</div>



                                </Col>
                                <Col md={2}></Col>
                            </div>


                            <Card.Body>
                                <Card.Header>
                                    <Card.Title as="h5" style={{ marginTop: '5px' }} >Purchase Orders</Card.Title>
                                </Card.Header>
                                <Table hover responsive>
                                    <thead>
                                        <tr>
                                            <th>#</th>
                                            {/* <th>Items</th> */}
                                            <th>Invoiced On</th>
                                            <th>Total Cost</th>
                                            <th>Status</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {PurchaseOrdersList || ''}
                                    </tbody>
                                </Table>
                                {isEmpty(purchaseOrdersArr) && !loading ? <><IsEmpty>Data not available</IsEmpty><br /><br /></> : ''}
                                {loading ? <Spinner /> : null}
                                <Paging instanceCount={instanceCount} />
                            </Card.Body>


                        </Card.Body>
                    </Card>
                </Col>
                <DeleteModal
                    show={deleteData.showModal}
                    onCancel={() => setDeleteData({ ...deleteData, showModal: false })}
                    onDelete={() => _delete(deleteData.vendorId)}
                />
            </Row>
        </>
    );
}

export default Vendor;
